.about__container {
    display: grid;
    grid-template-columns: 35% 50%;
    gap: 15%;
}

.cta {
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
}

.about__me {
    width: 80%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(45deg, transparent, var(--color-primary), transparent);
    display: grid;
    place-items: center;
    justify-content: center;
}

.about__me-image {
    border-radius: 2rem;
    overflow: hidden;
    transform: rotate(10deg);
    transition: var(--transition);
}

.about__me-image:hover {
    transform: rotate(0deg);
}

.hoverResume {
    width: 100%;
    height: 48%;
    background: black;
    align-content: center;
    position: relative;
    cursor: pointer;
}

#LinkIcon {
    width: 100%;
    height: 30%;
    margin-top: 4rem;
    position: absolute;
    color: transparent;
}

.resume__preview:hover #LinkIcon {
    width: 100%;
    height: 40%;
    padding-top: 8rem;
    margin-top: 4rem;
    position: absolute;
    color: var(--color-bg);
    cursor: pointer;
}

.resume__preview:hover .resumeImage {
    background-color: black;
    opacity: 30%;
    cursor: pointer;
}

.resume__preview {
    margin-top: 6rem;
    margin-bottom: 3rem;
    width: 80%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: var(--color-white);
    display: grid;
    place-items: center;
    justify-content: center;
}

.resume__preview-image {
    border-radius: 2rem;
    overflow: hidden;
    transform: rotate(10deg);
    transition: var(--transition);
}

.resume__preview:hover .resume__preview-image {
    transform: rotate(0deg);
}

.about__cards {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1.5rem;
}

.about__card {
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    transition: var(--transition);
}

.about__card:hover {
    background: var(--color-bg);
    border-color: var(--color-primary-variant);
    cursor: default;
}

.about__icon {
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}

.about__card h5 {
    font-size: 0.95rem;
}

.about__card small {
    font-size: 0.7rem;
    color: var(--color-light);
}

.about__content p {
    margin: 2rem 0 2.6rem;
    color: var(--color-light);
}

/********************************************

    MEDIA QUERIES (MEDIUM DEVICES)

********************************************/

@media screen and (max-width: 1024px) {
    .about__container {
        grid-template-columns: 1fr;
        gap: 0%;
    }

    .about__me {
        width: 50%;
        margin: 2rem auto 4rem;
    }

    .about__content p {
        margin: 1rem 0 1.5rem;
    }

    .resume__preview {
        width: 50%;
        margin: 2rem auto 4rem;
    }
}

/********************************************

    MEDIA QUERIES (SMALL DEVICES)

********************************************/

@media screen and (max-width: 600px) {
    .about__me {
        width: 65%;
        margin: 0 auto 3rem;
    }

    .resume__preview {
        width: 65%;
        margin: 0 auto 3rem;
    }

    .about__cards {
        grid-template-columns: 1fr 1fr;
        gap: 1rem;
    }

    .about__content {
        text-align: center;
    }

    .about__content p {
        margin: 1.5rem 0;
    }
}